	import helper from '../../common/helper.js';
	var _self;
	export default {
		data() {
			self = this;
			return {
				merchantname: '', //企业名
				artiperson: '', //法人代表
				mobile: '', //手机号
				password: '', //密码
				businesslicense: '', //营业执照
				conpassword: '', //确认密码
				nikename: '', //昵称
				code: "", //输入的验证码
				codes:'',
				codestr: '', //获取的随机数
				tipsCode: "获取验证码",
				timeNum: 60,
				clikType: false,
				timeOut: 60,
				img: '',
				imgs: '',    //识别后的营业执照地址
				MerName:'' ,// 识别出的企业名
				MerPer:'' // 识别出的企业名
			};

		},
		onLoad() {
			_self = this;
		},
		methods: {
			setData: function(obj) {
				let that = this;
				Object.keys(obj).forEach(function(key) {
					that.$set(that.$data, key, obj[key]);
				});
			},
			// 营业执照识别
			getImage: function() {
				var _this = this;
				uni.chooseImage({
					success: function(res) {
						_this.imgs = res.tempFilePaths[0];
						uni.showLoading({
							title: '识别中'
						});
						uni.uploadFile({
							url: helper.websiteUrl + 'annexes/businesslicense',
							filePath: res.tempFilePaths[0],
							fileType: "uploadFile",
							name: 'upload',
							formData: {
								loginMark: helper.getloginMark(),
								token: '',
								data: '{}'
							},
							success: function(resup) {
								var resdata = JSON.parse(resup.data);
								if (resdata.code == 200) {
									var url = 'https://cszx.yiruit.net';
									var imgurl = url + resdata.data.path;
									_this.img = imgurl;
									_this.MerName = resdata.data.name;    // 商家名称
									_this.MerPer = resdata.data.person;    // 法人名称
									uni.showToast({
										icon: 'none',
										title: '识别成功！'
									});
								} else {
									uni.hideLoading();
									uni.showToast({
										icon: 'none',
										title: resdata.info
									});
								}
								uni.hideLoading();
							},
							fail() {
								uni.hideLoading();
								uni.showToast({
									icon: 'none',
									title: '识别失败'
								});
							}
						});
						_this.setData({
							img: res.tempFilePaths[0],
						});
					},
				})
			},
			// 获取验证码
			openCodeDialog: function() {
				var that = this
				if (that.mobile == '') {
					uni.showToast({
						icon: 'none',
						title: '请输入你的手机号码'
					});
					return;
				} else if (!helper.checkmobile(that.mobile)) {
					uni.showToast({
						icon: 'none',
						title: '请输入正确的手机号码'
					});
					return;
				} else if (!that.clikType) {
					that.getTime();
					that.codestr = helper.MathRand(4);
					that.clikType = true;
					uni.request({
						url:helper.websiteUrl+'send/sendcode',
						method: 'GET',
						data: {
							loginMark: helper.getloginMark(),
							token: '',
							data: '{"phone": "' + self.mobile + '"}'
						},
						success: res => {
							if (res.data.code == 200) {
								this.codes = res.data.info;
							} else {
								uni.showToast({
									icon: 'none',
									title: '获取验证码失败,请重试'
								});
							}
						},
						fail: () => {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: '网络异常,请稍后重试'
							});
						}
					});
				}
			},
			// 验证码倒计时
			getTime() {
				var that = this
				var timer = setInterval(function() {
					that.timeNum--;
					if (that.timeNum <= 0) {
						that.tipsCode = "重新获取验证码";
						that.timeNum = that.timeOut;
						that.clikType = false;
						clearInterval(timer);
					} else {
						that.tipsCode = "重新发送" + that.timeNum + "秒";
					}
				}, 1000)
			},
			formSubmit: function(e) {
				var Plans = e.detail.value.plans;
				if (this.merchantname == '') {
					uni.showToast({
						icon: 'none',
						title: '请输入企业名'
					});
					return;
				}
				if (this.artiperson == '') {
					uni.showToast({
						icon: 'none',
						title: '请输入法人代表'
					});
					return;
				}
// 				if (this.artiperson != this.MerPer) {
// 					uni.showToast({
// 						icon: 'none',
// 						title: '请填写与营业执照上相同的法人'
// 					});
// 					return;
// 				}
			
				if (this.imgs == '') {
					uni.showToast({
						icon: 'none',
						title: '请上传营业执照'
					});
					return;
				}
				if (this.mobile == '') {
					uni.showToast({
						icon: 'none',
						title: '请输入你的手机号码'
					});
					return;
				}
				if (!helper.checkmobile(this.mobile)) {
					uni.showToast({
						icon: 'none',
						title: '请输入正确的手机号码'
					});
					return;
				}
				if (this.code == "" || this.code != this.codes || this.codes !=
					this.code) {
					uni.showToast({
						icon: 'none',
						title: '验证码错误'
					});
					return;
				}
				if (this.password.length < 6 || this.password.length > 12) {
					uni.showToast({
						icon: 'none',
						title: '请输入6-12个字符的密码'
					});
					return;
				}
				if (this.password != this.conpassword) {
					uni.showToast({
						icon: 'none',
						title: '密码确认错误'
					});
					return;
				}
				if (this.account == '') {
					uni.showToast({
						icon: 'none',
						title: '请输入平台账号'
					});
					return;
				}
				uni.showLoading({
					mask: true,
					title: '注册中，请稍候'
				});
				uni.request({
					url: helper.websiteUrl + 'remerchant/registration',
					data: {
						loginMark: helper.getloginMark(),
						token: '',
						data: '{"phone": "' + self.mobile + '","artiperson": "' + self.artiperson + '","password": "' + self.password +
							'","merchantname":"' + self.merchantname +
							'","businesslicense":"' + self.img + '"}'
					},
					method: 'GET',
					dataType: 'json',
					success: (res) => {
						uni.hideLoading();
						if (res.data.code == 200) {
							uni.showToast({
								icon: 'none',
								title: '注册成功，请登录',
								duration: 2500,
							});
							uni.removeStorageSync('sdlstate1');
							setTimeout(function() {
								uni.redirectTo({
									url: 'MerLogin'
								});
							}, 2800)
						} else {
							//console.log(res.data.info);
							uni.showToast({
								icon: 'none',
								title: res.data.info,
								duration: 2000,
							});
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试',
							duration: 2000,
						});
					}
				})
			},
			openlogin() { //打开登录页面 
				uni.navigateTo({
					url: 'MerLogin'
				});
			},

		},
	}
